@import '../../style/mixins.scss';

.category-up {
  display: flex;
  align-items: center;
  gap: 50px;
  background-color: #885133;
  padding: 15px 10px;
  justify-content: center;

  h1 {
    color: white;
    font-size: 60px;
    margin: auto;
    align-self: flex-start;

    @include mobile-only {
      font-size: 30px;
    }
  }

  button {
    background-color: rgb(0, 0, 0, 50%);
    padding: 10px;
    border: none;
    border-radius: 50%;
    align-self: flex-start;
    margin-right: auto;

    @include mobile-only {
      padding: 7.5px;
    }

    &:hover {
      cursor: pointer;
    }

    svg {
      fill: white;
      width: 25px;

      @include mobile-only {
        width: 12.5px;
      }
    }
  }
}

.category-card {
  background-color: #5c2c00;
  display: flex;
  flex-wrap: nowrap;
  padding: 50px 25px;
  justify-content: center;

  @include mobile-only {
    flex-direction: column;
    padding: 0;
  }

  figure {
    flex: 1;
    background-color: #ffefcf;
    max-width: 430px;
    border-radius: 15px;

    img {
      width: 100%;
      height: 100%;
      max-height: 380px;
      object-fit: cover;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }

    figcaption {
      color: #361500;
      padding: 10px;
      flex: 1;

      h3 {
        text-align: center;
        font-size: 24px;

        @include mobile-only {
          font-size: 22px;
        }
      }

      p {
        font-size: 18px;
      }
    }
  }
}
